"use client";

import React, { useRef } from "react";
import Sidebar from "@/components/sidebar";
import VaccinePage from "./page1";
import Footer from "@/components/footer";

// Import PDF libraries
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
import VaccinePage2 from "./page2";
import { useProtectedRoutesApi } from "@/libraries/API/ProtectedRoute/secureRoutes";
import { useQuery } from "@tanstack/react-query";

const MainVaccinePage = () => {
  const { infantDataDownload } = useProtectedRoutesApi();

  const { data, isLoading, isError, error } = useQuery({
    queryKey: ["infant-data"],
    queryFn: infantDataDownload,
  });

  const pdfRef = useRef(null);

  const handleSavePDF = async () => {
    if (!pdfRef.current) return;

    // 1) Render the PDF area to a canvas with a higher scale for better clarity
    const canvas = await html2canvas(pdfRef.current, {
      scale: 2, // Increase this if you want higher resolution
      useCORS: true, // Helps when images are loaded from external URLs
      backgroundColor: null, // Helps preserve transparent or custom backgrounds
    });

    // 2) Convert canvas to an image
    const imgData = canvas.toDataURL("image/png");

    // 3) Create the jsPDF instance
    //    Note that using "pt" (points) as the unit is often simpler when dealing
    //    with precise pixel-to-point conversions, but you can stick to "mm" if you prefer.
    const pdf = new jsPDF("p", "pt", "a4"); // "a4" in points is ~ 595 x 842

    // 4) Calculate image dimensions for the PDF
    const pdfWidth = pdf.internal.pageSize.getWidth();
    const pdfHeight = pdf.internal.pageSize.getHeight();

    // The actual dimensions of the rendered canvas
    const imgProps = pdf.getImageProperties(imgData);
    const canvasWidth = imgProps.width;
    const canvasHeight = imgProps.height;

    // We want to scale the canvas image to fit the PDF's width
    const imageAspectRatio = canvasHeight / canvasWidth;
    let renderedPdfHeight = pdfWidth * imageAspectRatio;

    // 5) If the rendered image is taller than one page, we slice it into multiple pages
    let heightLeft = renderedPdfHeight;
    let position = 0;

    // Because we are using "pt", be sure to use points consistently:
    // pdfWidth is in points, pdfHeight is in points, etc.
    // We'll keep adding pages while there's height left to render.
    while (heightLeft > 0) {
      // If the remaining content is still bigger than one page, slice it
      pdf.addImage(imgData, "PNG", 0, position, pdfWidth, renderedPdfHeight);

      heightLeft -= pdfHeight;

      // If there's more content left, add a new page
      if (heightLeft > 0) {
        position = -pdfHeight; // Move the next slice upward
        pdf.addPage();
      }
    }

    // 6) Save the PDF
    pdf.save("vaccine-page.pdf");
  };

  return (
    <div className="grid grid-cols-[250px_1fr] grid-rows-[1fr_auto] min-h-screen">
      {/* Sidebar */}
      <Sidebar />
      <main className="p-8 sm:p-20 font-[family-name:var(--font-geist-sans)] flex flex-col gap-8">
        {/* Save to PDF Button */}
        <button
          onClick={handleSavePDF}
          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded"
        >
          Save to PDF
        </button>

        {/* Wrap VaccinePage in a container with a ref to capture it */}
        <div ref={pdfRef}>
          <VaccinePage />
          <VaccinePage2 />
          {JSON.stringify(data?.data)}
        </div>
      </main>

      <Footer />
    </div>
  );
};

export default MainVaccinePage;
